html {
    scroll-behavior: smooth;
}
body {
    background-color: #ddd;
    text-align: center;
}
#root {
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
#editor {
    background-color: #002b3f;
    color: #fff;
    margin: 10px;
    padding: 10px;
    width: 70%;
    max-width: 700px;
    max-height: 700px;
    min-width: 300px;
    min-height: 300px;
    resize: none;
}
#markdown-preview {
    background-color: #d5f1fe;
    margin: 10px;
    padding: 10px;
    width: 70%;
    max-width: 700px;
    min-width: 300px;
    text-align: left;
}
a {
    color: #005f8b;
    font-size: 1.5em;
}
h1, h2, h3 {
    text-align: center;
}
table {
    border-collapse: collapse;
}
table tr, table th, table td {
    padding: 7px;
    border: solid 2px #000;
}
blockquote {
    background-image: linear-gradient(to right, #fff 70%, #fff0);
    padding: 0;
    padding: 5px;
    text-indent: 2em;
    border-left: 5px solid #00f;
}
pre, code {
    background-color: #002b3f;
    color: #fff;
}
pre {
    padding: 10px;
    overflow-x: scroll;
}
img {
    max-width: 100%;
}